<template>
	<div>
		<div id="viewer"></div>
		<el-tag>当前标记ID:{{nowMarkers}}</el-tag>
		<el-button type="primary" @click="addMark">新增一个标记</el-button>
		<el-button type="primary" @click="chooseMark">{{canChoose?'取消选择':'选择标记'}}</el-button>
		<el-button type="danger" @click="delMark">删除标记</el-button>
		<el-select v-model="value" placeholder="请选择新增标记类型">
			<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
		</el-select>
		<el-button type="success" @click="stopPoint">完成</el-button>
		<el-button type="danger" @click="backPoint">撤销</el-button>
		<el-input style="width: 10rem;" v-model="input" v-if="value == 'html'" placeholder="请输入内容"></el-input>
		<el-input style="width: 10rem;" v-model="fontSize" v-if="value == 'html'" placeholder="文字大小"></el-input>
		<el-input style="width: 10rem;" v-model="lineWidth" @input="isInput" v-if="value == 'polylineRad'" placeholder="请输入线段宽度"></el-input>
	</div>
</template>

<script>
var numberObj = {
	polygonRad: 1,
	polylineRad: 2,
	image: 1,
	html: 1,
	hot: 1
};
const axios = require('axios');
import * as PhotoSphereViewer from 'photo-sphere-viewer';
import MarkersPlugin from 'photo-sphere-viewer/dist/plugins/markers';
export default {
	data() {
		return {
			canChoose:false,
			lineWidth: 10,
			fontSize: 15,
			input: '',
			options: [
				{
					value: 'polygonRad',
					label: '多边形'
				},
				{
					value: 'polylineRad',
					label: '线段'
				},
				{
					value: 'image',
					label: '图片'
				},
				{
					value: 'html',
					label: '文字'
				},
				{
					value: 'hot',
					label: '热点'
				}
			],
			value: 'polygonRad',
			longitude: '',
			latitude: '',
			isImg: require('../assets/tips1.png'),
			defaultLong: 0,
			defaultLat: 0,
			nowMarkers:''
		};
	},
	mounted() {
		axios.post('https://admin.keyland.cn/sysmanage/api/account/tokenAuth', {
		    account: "yangjian",
		    passWord: "yangjian",
		    systemId: 1003
		  })
		  .then(function (response) {
		    console.log(response);
		  })
		  .catch(function (error) {
		    console.log(error);
		  });
		this.isAdd = false;
		this.polygonRad = [];
		this.viewer = new PhotoSphereViewer.Viewer({
			container: document.querySelector('#viewer'),
			panorama: require('../assets/Bryce-Canyon-By-Jess-Beauchemin.jpg'),
			defaultLong: this.defaultLong,
			defaultLat: this.defaultLat,
			plugins: [
				[
					MarkersPlugin,
					{
						markers: [
							{
								id: 'circle',
								tooltip: 'A circle of radius 30',
								circle: 30,
								svgStyle: {
									fill: 'rgba(255,255,0,0.3)',
									stroke: 'yellow',
									strokeWidth: '2px'
								},
								longitude: -0.5,
								latitude: -0.28,
								anchor: 'center right'
							},
							{
								id: 'rect',
								// tooltip:'<div>宽度<input v-model="width" @input="isInput" placeholder="宽度" /></div>',
								rect: [100, 50],
								svgStyle: {
									fill: 'url(#points)', //'rgba(255,0,0,0.3)',
									stroke: 'rgba(255, 0, 50, 0.8)',
									strokeWidth: '2px'
									// fill:'rgba(255,255,0,0.3)',
									// stroke:'yellow',
									// strokeWidth:'2px',
								},
								longitude: '-100deg',
								latitude: '45deg',
								scale: [0.5, 1],
								anchor: 'center bottom'
							},
							{
								id: 'img1',
								image: require('../assets/tips1.png'),
								width: 189,
								height: 71,
								longitude: 0.15,
								latitude: 0.2,
								anchor: 'center top',
								content: '121221'
							},
							{
								id: 'polyline',
								polylineRad: [[5.924, 0.064], [5.859, -0.061], [5.71, -0.132], [5.41, -0.287], [4.329, -0.49], [3.838, -0.37], [3.725, -0.241]],
								svgStyle: {
									stroke: 'rgba(80, 100, 50, 0.8)',
									strokeLinecap: 'round',
									strokeLinejoin: 'round',
									strokeWidth: '10px'
								}
								// tooltip:'This is a track'
							}
						]
					}
				]
			]
		});
		console.log(this.viewer);
		this.viewer.on('click', (e, data) => {
			console.log(data);
			console.log(`${data.rightclick ? 'right clicked' : 'clicked'} at longitude: ${data.longitude} latitude: ${data.latitude}`);
			if (this.isAdd) {
				this.polygonRad.push([data.longitude, data.latitude]);
				this.changeMark();
			}
		});
		this.markersPlugin = this.viewer.getPlugin(MarkersPlugin);
		this.markersPlugin.on('over-marker', (e, marker) => {
			console.log(marker);
			if (marker.config.svgStyle && marker.config.svgStyle.fill) {
				let updateTemp = {
					id: marker.id,
					svgStyle: {
						fill: 'rgba(255,255,255,0.6)'
					}
				};
				this.markersPlugin.updateMarker(updateTemp);
			}
		});
		this.markersPlugin.on('leave-marker', (e, marker) => {
			console.log(`Cursor is leave-marker marker ${marker.id}`);
			if (marker.config.svgStyle && marker.config.svgStyle.fill) {
				let updateTemp = {
					id: marker.id,
					svgStyle: {
						fill: 'rgba(255,255,0,0.3)'
					}
				};
				this.markersPlugin.updateMarker(updateTemp);
			}
		});
		var that = this;
		this.markersPlugin.on('select-marker', (e, marker, data) => {
			if (marker.data && marker.data.id) {
				this.open(marker.data.id);
			}
			if(this.canChoose) {
				this.nowMarkers = marker.config.id;
			}
			// if (data.dblclick) {
			// 	this.markersPlugin.removeMarker(marker);
			// }
			// this.nowMarkers = marker.config.id;
		});
		// this.markersPlugin.toggleAllTooltips();
	},
	methods: {
		delMark(){
			this.markersPlugin.removeMarker(this.nowMarkers);
		},
		chooseMark(){
			this.canChoose  = !this.canChoose
		},
		isInput(item){
			let updateTemp = {
				id: this.nowMarkers
			};
			updateTemp['svgStyle'] = {
				strokeWidth: `${item}px`
			}
			this.markersPlugin.updateMarker(updateTemp);
		},
		open(id) {
			// this.$alert(`你点击了热点`, '标题名称', {
			// 	confirmButtonText: '确定',
			// 	callback: action => {}
			// });
			this.viewer.setPanorama(require('../assets/2294472375_24a3b8ef46_o.jpg'))
		},
		backPoint() {
			this.polygonRad.splice(-1, 1);
			this.changeMark();
		},
		stopPoint() {
			this.isAdd = false;
		},
		addMark() {
			this.polygonRad = [];
			this.nowMarkers = `markers${Math.random(9999)}`;
			this.isAdd = true;
		},
		changeMark() {
			console.log(this.polygonRad);
			let tempList = JSON.parse(JSON.stringify(this.polygonRad));
			console.log(tempList);
			if (tempList.length == 0) {
				this.markersPlugin.removeMarker(this.nowMarkers);
			} else if (tempList.length == numberObj[this.value]) {
				let svgPolygonRad = {
					polygonRad: {
						id: this.nowMarkers,
						tooltip:
							'<div style="display: flex;align-items: center;justify-content: center;flex-direction: column;height:12.5rem;"><img style="width: 5rem;height: 5rem;" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3184273983,1374445179&fm=26&gp=0.jpg" /><div>介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍</div></div>',
						svgStyle: {
							fill: 'rgba(255,255,0,0.3)',
							stroke: 'yellow',
							strokeWidth: '2px'
						}
					},
					polylineRad: {
						id: this.nowMarkers,
						// tooltip:'<div style="display: flex;align-items: center;justify-content: center;flex-direction: column;height:12.5rem;"><img style="width: 5rem;height: 5rem;" src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3184273983,1374445179&fm=26&gp=0.jpg" /><div>介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍</div></div>',
						svgStyle: {
							stroke: 'rgba(80, 100, 50, 0.8)',
							strokeLinecap: 'round',
							strokeLinejoin: 'round',
							strokeWidth: `${this.lineWidth}px`
						}
					},
					image: {
						id: this.nowMarkers,
						image: this.isImg,
						width: 150,
						height: 150,
						longitude: tempList[0][0],
						latitude: tempList[0][1],
						anchor: 'center top'
					},
					html: {
						id: this.nowMarkers,
						html: `<div style="padding: 1.25rem;background-color: rgba(0,0,0,.6);border-radius: 5px;color: white;font-size:${this.fontSize}px">${this.input}</div>`,
						longitude: tempList[0][0],
						latitude: tempList[0][1]
					},
					hot: {
						id: this.nowMarkers,
						html: '<div class="canClick"></div>',
						longitude: tempList[0][0],
						latitude: tempList[0][1],
						data: {
							id: 1
						}
					}
				};
				let temp = svgPolygonRad[this.value];
				if (this.value == 'polylineRad' || this.value == 'polygonRad') temp[this.value] = tempList;
				console.log(temp);
				this.markersPlugin.addMarker(temp);
				if(this.value == 'hot' || this.value == 'image' || this.value == 'html'){
					this.addMark();
				}
			} else {
				if (this.value == 'polylineRad' || this.value == 'polygonRad') {
					let updateTemp = {
						id: this.nowMarkers
					};
					updateTemp[this.value] = tempList;
					this.markersPlugin.updateMarker(updateTemp);
				}
			}
		}
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@keyframes shockwave {
	0% {
		-webkit-transform: scale(1);
		transform: scale(1);
		box-shadow: 0 0 0.125rem rgba(255, 255, 255, 0.6), inset 0 0 0.0625rem rgba(255, 255, 255, 0.6);
		opacity: 1;
	}
	95% {
		box-shadow: 0 0 3.125rem rgba(255, 255, 255, 0.6), inset 0 0 1.875rem rgba(255, 255, 255, 0.6);
		opacity: 0;
	}
	100% {
		-webkit-transform: scale(2.25);
		transform: scale(2.25);
		opacity: 0;
	}
}
.canClick::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	border-radius: 50%;
	-webkit-animation: shockwave 3s 0.5s ease-out infinite;
	animation: shockwave 3s 0.5s ease-out infinite;
}
.canClick::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	border-radius: 50%;
	-webkit-animation: shockwave 3s 0.65s ease-out infinite;
	animation: shockwave 3s 0.65s ease-out infinite;
}
.canClick {
	position: relative;
	width: 0.925rem;
	height: 0.925rem;
	background-color: white;
	border-radius: 50%;
	cursor: pointer;
}
.isBlack {
	font-size: 100px;
}
#viewer {
	width: 100vw;
	height: 80vh;
}
</style>
